@font-face {
  font-family: 'icomoon';
  src:  url('icomoon.eot?12moml');
  src:  url('icomoon.eot?12moml#iefix') format('embedded-opentype'),
    url('icomoon.ttf?12moml') format('truetype'),
    url('icomoon.woff?12moml') format('woff'),
    url('icomoon.svg?12moml#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-home:before {
  content: "\f015";
}
.icon-phone:before {
  content: "\f095";
}
.icon-envelope:before {
  content: "\f0e0";
}
